<template>
  <div class="">
    <h1>Lista della tabella coda</h1>
    <hr />
    <div class="row">
      <div class="col-lg-4">
        <div class="form-group">
          <label for="">Stato</label>
          <select v-model="status" class="form-control">
            <option value="null" selected>Seleziona</option>
            <option
              v-for="(item, index) in statusList"
              :key="index"
              :value="item.id"
            >
              {{ item.message }}
            </option>
          </select>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-4">
        <div class="form-group">
          <button
            class="btn btn-success"
            @click="submit"
            v-on:keyup.enter="submit"
          >
            Aggiorna
          </button>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-12 col-sm-12">
        <table class="table">
          <thead>
            <tr>
              <th>ID</th>
              <th>IDUtente</th>
              <th>IDAzienda</th>
              <th>Modello</th>
              <th>NomeFilePdV</th>
              <th>DataOraInsert</th>
              <th>DataOraUpdate</th>
              <th>Stato</th>
              <th>DettagliErrore</th>
              <th>Motivo Scarto</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in data" :key="index">
              <td>{{ item.ID }}</td>
              <td>{{ item.IDUtente }}</td>
              <td>{{ item.IDAzienda }}</td>
              <td>{{ item.Modello }}</td>
              <td>{{ item.NomeFilePdV }}</td>
              <td>{{ item.DataOraInsert }}</td>
              <td>{{ item.DataOraUpdate }}</td>
              <td>{{ item.Stato }}</td>
              <td>{{ item.DettagliErrore }}</td>
              <td>
                <i
                  class="fa fa-external-link"
                  aria-hidden="true"
                  @click="openDetail(item.NomeFilePdV)"
                ></i>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <v-pagination
      v-model="currentPage"
      :page-count="totalPages"
      :classes="bootstrapPaginationClasses"
      :labels="paginationAnchorTexts"
      @change="submit"
    ></v-pagination>
  </div>
</template>
<script>
import vPagination from "vue-plain-pagination";

export default {
  components: { vPagination },
  data() {
    return {
      currentPage: 1,
      totalPages: 30,
      bootstrapPaginationClasses: {
        ul: "pagination",
        li: "page-item",
        liActive: "active",
        liDisable: "disabled",
        button: "page-link",
      },
      paginationAnchorTexts: {
        first: "First",
        prev: "Previous",
        next: "Next",
        last: "Last",
      },

      data: [],
      status: null,
      statusList: [
        { id: 1, message: "1 - in coda" },
        { id: 2, message: "2 - versando" },
        { id: 3, message: "3 - versato" },
        { id: 4, message: "4 - conservando" },
        { id: 5, message: "5 - conservato" },
        { id: 6, message: "6 - scartato" },
        { id: 7, message: "7 - errore" },
      ],
    };
  },
  mounted() {
    this.submit();
  },
  methods: {
    submit() {
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=retentionQueue&offset=" +
        (this.currentPage - 1) * 100 +
        (this.status == null ? "" : "&status=" + this.status);
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        this.data = r.data;
        this.$store.dispatch("loaded");
      });
    },
    openDetail(a) {
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=getTxtStream&pdv=" +
        a;
      window.open(link);
    },
  },
};
</script>